$time-of-animation: 300ms;

.icon {
  display: block;
  width: 24px;
  height: 24px;
  transition: all $time-of-animation;

  &:hover {
    transform: scale(1.2);
  }

  @include on-tablet {
    width: 32px;
    height: 32px;
  }

  &--phone {
    background-image: url("../images/icon/icon-phone-call.png");
    background-position: center;
  }

  &--menu {
    background-image: url("../images/icon/icon-burger-menu.png");
    background-position: center;
    background-repeat: no-repeat;

    &:hover {
      background-image: url("../images/icon/icon-burger-menu-clicked.png");
      background-repeat: no-repeat;
    }
  }

  &--close {
    background-image: url("../images/icon/icon-close.png");
    background-position: center;
  }
}

.menu__contacts--header {
  align-items: end;
  opacity: 0;
  position: absolute;
  top: -45px;
  right: 140px;
  white-space: nowrap;
  transition: opacity 300ms;
}

@include on-desktop {
  .header__icons {
    position: relative;

    > div:hover .menu__contacts--header {
      opacity: 1;
      align-items: end;
    }
  }
}
